<script>
import { v4 as uuidv4 } from 'uuid';
 let trade = {};
 trade.scrip = ""
 trade.spcredit = ""
 trade.bpdebit = ""
 

 $: if (trade.scrip != "") {
     let str = trade.scrip;
     trade.scrip = str.toUpperCase();
 }


 $: if (trade.spcredit >= -0.3 && trade.bpdebit >= -0.3){
     trade.pnl = trade.spcredit - trade.bpdebit;
     trade.ntpnl = trade.pnl - trade.charges;
 }




 function validatePosition(){
   trade.uid = localStorage.getItem("uid");
   trade.oid = uuidv4();
   fetch('http://localhost:3000/data/create', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json'
      // 'Content-Type': 'application/x-www-form-urlencoded',
    },
    body: JSON.stringify(trade)
  }).then(response =>{
    if(response.status == 201){
       trade = {}
   trade.scrip = ""
 trade.spcredit = ""
 trade.bpdebit = ""
   document.getElementById("closeModal").click()
    }else{
      alert("Failed");
    }
  })
  
 }

</script>

<div
  class="modal modal-blur fade"
  id="modal-add"
  tabindex="-1"
  role="dialog"
  aria-hidden="true"
>
  <div class="modal-dialog modal-dialog-centered" role="document">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title">Add Trade</h5>
        <button
          type="button"
          class="btn-close"
          id="closeModal"
          data-bs-dismiss="modal"
          aria-label="Close"
        />
      </div>
      <div class="modal-body">
        
        <div class="row">
          <div class="col-lg-6">
            <div class="mb-3">
            <span class="form-label">Scrip</span>
              <div class="input-group input-group-flat">
                <input
                  type="text"
                  bind:value={trade.scrip}
                  class="form-control"
                  placeholder="NIFTY"
                />
              </div>
            </div>
          </div>

        <div class="col-lg-6">
            <div class="mb-3">
            <span class="form-label">Contract Open Date</span>
              <div class="input-group input-group-flat">
                <input
                  type="date"
                  class="form-control"
                  placeholder="Contract Open Date"
                  id="datepicker-icon-prepend"
                  bind:value={trade.conopendt}
                />
              </div>
            </div>
          </div>

           <div class="col-lg-6">
            <div class="mb-3">
            <span class="form-label">Contract Expiration</span>
              <div class="input-group input-group-flat">
                <input
                  type="date"
                  class="form-control"
                  placeholder="Contract Open Date"
                  id="datepicker-icon-prepend"
                  bind:value={trade.conexpndt}
                />
              </div>
            </div>
          </div>

          <div class="col-lg-3">
            <div class="mb-3">
            <span class="form-label">Lots</span>
              <div class="input-group input-group-flat">
                <input
                  type="number"
                  bind:value={trade.lots}
                  class="form-control"
                  placeholder="Lots"
                />
              </div>
            </div>
          </div>

          <div class="col-lg-3">
            <div class="mb-3">
            <span class="form-label">Lot Size</span>
              <div class="input-group input-group-flat">
                <input
                  type="number"
                  bind:value={trade.lotsize}
                  class="form-control"
                  placeholder="Lot Size"
                />
              </div>
            </div>
          </div>

           <div class="col-lg-3">
            <div class="mb-3">
            <span class="form-label">Option Type </span>
              <select
                class="form-select"
                bind:value={trade.type}
              >
                <option value="PUT SELL" selected>PUT SELL</option>
                <option value="CALL SELL">CALL SELL</option>
                <option value="PUT BUY">PUT BUY</option>
                <option value="CALL BUY">CALL BUY</option>
              </select>
            </div>
          </div>


          <div class="col-lg-3">
            <div class="mb-3">
            <span class="form-label">Strike Price</span>
              <div class="input-group input-group-flat">
                <input
                  type="number"
                  bind:value={trade.strikeprice}
                  class="form-control"
                  placeholder="Strike Price"
                />
              </div>
            </div>
          </div>

          <div class="col-lg-3">
            <div class="mb-3">
            <span class="form-label">Sell Price(Credit)</span>
              <div class="input-group input-group-flat">
                <input
                  type="number"
                  bind:value={trade.spcredit}
                  class="form-control"
                  placeholder="Credit"
                />
              </div>
            </div>
          </div>

            <div class="col-lg-3">
            <div class="mb-3">
            <span class="form-label">Buy Price(Debit)</span>
              <div class="input-group input-group-flat">
                <input
                  type="number"
                  bind:value={trade.bpdebit}
                  class="form-control"
                  placeholder="Debit"
                />
              </div>
            </div>
          </div>

           <div class="col-lg-6">
            <div class="mb-3">
            <span class="form-label">Contract Close Date</span>
              <div class="input-group input-group-flat">
                <input
                  type="date"
                  class="form-control"
                  placeholder="Contract Open Date"
                  id="datepicker-icon-prepend"
                  bind:value={trade.conclsdt}
                />
              </div>
            </div>
          </div>

          <div class="col-lg-3">
            <div class="mb-3">
            <span class="form-label">Margin Blocked</span>
              <div class="input-group input-group-flat">
                <input
                  type="number"
                  bind:value={trade.mrgblck}
                  class="form-control"
                  placeholder="Margin Blocked"
                />
              </div>
            </div>
          </div>

          <div class="col-lg-3">
            <div class="mb-3">
            <span class="form-label">PnL</span>
              <div class="input-group input-group-flat">
                <input
                  type="number"
                  bind:value={trade.pnl}
                  class="form-control"
                  placeholder="PnL"
                  disabled
                />
              </div>
            </div>
          </div>

           <div class="col-lg-3">
            <div class="mb-3">
            <span class="form-label">Charges</span>
              <div class="input-group input-group-flat">
                <input
                  type="number"
                  bind:value={trade.charges}
                  class="form-control"
                  placeholder="Charges"
                />
              </div>
            </div>
          </div>



          <div class="col-lg-3">
            <div class="mb-3">
            <span class="form-label">Net PnL</span>
              <div class="input-group input-group-flat">
                <input
                  type="number"
                  bind:value={trade.ntpnl}
                  class="form-control"
                  placeholder="Net PnL"
                  disabled
                />
              </div>
            </div>
          </div>

          <div class="col-lg-3">
            <div class="mb-3">
            <span class="form-label">Spot Price</span>
              <div class="input-group input-group-flat">
                <input
                  type="number"
                  bind:value={trade.sptprice}
                  class="form-control"
                  placeholder="Spot Price"
                />
              </div>
            </div>
          </div>

          <div class="col-lg-3">
            <div class="mb-3">
            <span class="form-label">Chance of Profit</span>
              <div class="input-group input-group-flat">
                <input
                  type="number"
                  bind:value={trade.cop}
                  class="form-control"
                  placeholder="Chance %"
                  
                />
              </div>
            </div>
          </div>









        </div>
      </div>

      <!--Second Part-->

      <div class="modal-footer">
        <button class="btn btn-danger" data-bs-dismiss="modal">
          Cancel
        </button>
        <button class="btn btn-primary ms-auto" on:click={validatePosition}>
          <!-- Download SVG icon from http://tabler-icons.io/i/plus -->
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="icon"
            width="24"
            height="24"
            viewBox="0 0 24 24"
            stroke-width="2"
            stroke="currentColor"
            fill="none"
            stroke-linecap="round"
            stroke-linejoin="round"
            ><path stroke="none" d="M0 0h24v24H0z" fill="none" /><line
              x1="12"
              y1="5"
              x2="12"
              y2="19"
            /><line x1="5" y1="12" x2="19" y2="12" /></svg
          >
          Add
        </button>
      </div>
    </div>
  </div>
</div>
