<script>
  let active = {};
  let customlink = `/custom?strategy=%7B"positions":%5B%5D,"hlperiod":"intraday","squareoff":"oneleg"%7D`;

  $: setActive();

  function setActive() {
    if (window.location.pathname === "/custom") {
      active = {};
      active.custom = "active";
    } else if (window.location.pathname === "/reports") {
      active = {};
      active.reports = "active";
    }
  }

  function toCustom() {
    active = {};
    active.custom = "active";
  }
  function toReports() {
    active = {};
    active.reports = "active";
  }
</script>

