<script>
  import { createEventDispatcher } from "svelte";

  const dispatch = createEventDispatcher();
  let mode;

  let userphone = localStorage.getItem("phonenumber");

  mode = localStorage.getItem("mode");
  if (!mode) {
    mode = "light";
  }

  function toDark() {
    mode = "dark";
    localStorage.setItem("mode", "dark");
    dispatch("mode", mode);
  }

  function toLight() {
    mode = "light";
    localStorage.setItem("mode", "light");
    dispatch("mode", mode);
  }
</script>

