<script>
    import Create from "./Routes/Create.svelte";
    import Header from "./Components/Header.svelte";
  import Navbar from "./Components/Navbar.svelte";
import Progress from "./Components/Progress.svelte";
  import Login from "./Routes/auth/Login.svelte";
  import { users } from "./stores";
  import auth from "./firebase";

  let user_value;
  let showloginpage = false;

  const unsubscribe = users.subscribe((value) => {
    user_value = value;
  });

  auth.onAuthStateChanged((user) => {
    if (user) {
      localStorage.setItem("userprofile",JSON.stringify(user))
      localStorage.setItem("phonenumber",user.phoneNumber);
      localStorage.setItem("uid",user.uid);
      users.set({ ...$users, loggedIn: true });
      auth.currentUser
        .getIdToken(/* forceRefresh */ true)
        .then(function (idToken) {
          console.log(idToken);
        })
        .catch(function (error) {
          // Handle error
        });
    } else {
      showloginpage = true;
    }
  });


  let mode;

  let setMode;

  let page;
  mode = localStorage.getItem("mode");
  function changeMode() {
    mode = localStorage.getItem("mode");
    console.log(mode);
  }

  $: if (mode == "dark") {
    setMode = "theme-dark";
  } else {
    setMode = "";
  }

  
</script>


<div class="antialiased {setMode}">
{#if user_value.loggedIn == true}
    <div class="wrapper">
      <div class="sticky-top">
        <Header on:mode={changeMode} />
        <Navbar />
      </div>
      <div class="page-wrapper">
      <!-- <svelte:component this={page} /> -->  
      <Create/>
      </div>
    </div>
 {/if}
  
  {#if user_value.loggedIn == false}
    {#if showloginpage == true}
      <Login />
    {/if}
    {#if showloginpage == false}
      <Progress />
    {/if}
 {/if}
</div>

